.orderSuccess{
    margin:auto;
    text-align:center;
    padding:10vmax;
    height:50vh;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}
.orderSuccess > svg{
    font-size: 7vmax;
    color: tomato;
}
.orderSuccess > p{
    font-size:2vmax;
}

.orderSuccess > a{
    background-color: rgb(51, 51, 51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
    margin: 2vmax;
}

@media screen and (max-width:600px){
    .orderSuccess{
        margin:2vmax;
        padding: 3vw 6vw;
        font:400 4vw "Roboto";
    }
    .orderSuccess > svg{
        font-size: 20vw;
    }
    .orderSuccess > p{
        font-size:5vw;
        margin: 2vmax;
    }
}